

.content_component{
    display: flex;
    background-color: blueviolet;
    width:80%;
    height:90%;
    margin-left:10%;
    flex-direction: column;
}
center{
    text-align: center;
}
.card-header_{
    color: white;
    background-color:rgb(9, 78, 44);
     font-size: 1.2rem;
   font-weight: bolder;
    padding-top:1.4rem;
    padding-bottom:1.4rem;
}
#location_c{
    padding-left:2vw;
    padding-right:2vw;
    margin-top: 1rem;
   align-self: center;
    width:50%;
    font-size: 1.2rem;
}


#tableShow td a{
color: white;
}

#tableShow{
    overflow:scroll;
    max-width:100vw;
    max-height:80vh;
}
#tableShow table#tbl th{
  background-color:rgb(27, 146, 167);
  color:white;
}

.tableDiscription{
    color:white;
    display: flex;
    flex-wrap: wrap;
    background-color: rgb(50, 53, 224);
    font-size:1.5rem;
    padding:0.7rem;
    margin-top:0.5rem;
    font-weight: bolder;
    width:100vw;
    overflow-x: scroll;
    max-width: 100vw;
}

span#btn{
    margin-left:5vw;
    font-weight:bold;
    font-family:fantasy;
    color:white;
    background-color:rgb(147, 46, 194);
    
}

span#btn i{
    margin-right:0.4rem;
}



#sortByLatest{
    background-color:rgb(22, 179, 74);
    color:white;
}

#sortByOld{
    background-color:rgb(160, 92, 3);
    color:white;

}

div#spinners{
    align-content: center;
    height:80vh;
    text-align: center;
}


.card_header_ span{
    margin:0.5rem;
}
.card_header_ button{
    font-size:0.7rem;
}


button#typeBtn{
    margin-right:1vw;
}
@media screen and (max-width:500px){
  p.card-header_{
      font-size: 5vw;
  }
  #tableShow table#tbl th{
      font-size:4vw;

  }
  .tableDiscription{
      display: grid;
      font-size:1rem;
      grid-row-gap: 0.6rem6
  }
  #tableShow tr td{
      font-size:0.8rem;
  }
    #disc{
        grid-row: 1/2;
        grid-column: 1/5;
    }
  #sortByLatest{
    font-weight: bold;

      grid-row: 2/3;
      font-size:0.6rem;
      grid-column: 2/3;

  }
  #sortByOld{
       grid-row:2/3;
       border-radius: 50%;

       font-weight: bold;

      font-size:0.6rem;
      grid-column: 3/4;
  }
      
  span#btn{

    grid-row:2/3;

    font-weight: bold;
    font-size:0.5rem;
      grid-column: 1/2;
  }
}